	module.exports = {"layout":"post","title":"ทดสอบ local npm package ด้วย yarn link","summary":"เคยมั้ยครับที่โปรเจคของเราเรียกใช้ library ของคนอื่นแล้วพบว่ามันมีบั๊กต้องแก้ไข หรือไม่ก็เราอยากจะเพิ่มฟังก์ชันให้มันทำอะไรเพิ่มซักอย่าง เราก็เลย clone repo ของเค้าลงมาบนเครื่อง local แล้วทำการแก้ พอแก้เสร็จก็สงสัยว่าแล้วเราจะทดสอบ library ที่แก้แล้วนั้น กับโปรเจคของเรายังไง? (อ่านต่อ)","tags":["npmjs","yarn"],"img":"https://raw.githubusercontent.com/LarngearTech/codesheep.io/master/pages/2016-12-10-yarn-link/yarn.jpg","author":"Supasate Choochaisri","body":"<p>เคยมั้ยครับที่โปรเจคของเราเรียกใช้ library ของคนอื่นแล้วพบว่ามันมีบั๊กต้องแก้ไข หรือไม่ก็เราอยากจะเพิ่มฟังก์ชันให้มันทำอะไรเพิ่มซักอย่าง เราก็เลย clone repo ของเค้าลงมาบนเครื่อง local แล้วทำการแก้ พอแก้เสร็จก็สงสัยว่าแล้วเราจะทดสอบ library ที่แก้แล้วนั้น กับโปรเจคของเรายังไง?</p>\n<p>บทความนี้จะแนะนำวิธีหนึ่งที่นิยมใช้กันคือใช้ <code>yarn link</code> (หรือ <code>npm link</code> แต่ถึงตอนนี้ไม่น่าจะมีใครอยากใช้ <code>npm</code> แล้ว)</p>\n<h2>Learn by doing</h2>\n<p>มาเรียนรู้โดยลองทำโปรเจคสั้นๆเล่นกันดูนะครับ โดยเราจะลองทำการเพิ่มฟังก์ชันง่ายๆให้ <a href=\"https://github.com/lodash/lodash\">lodash</a> กัน</p>\n<p>คาดว่าทุกคนคงรู้จัก lodash นะครับ เป็น library สารพัดประโยชน์ แต่มันดันขาดฟังก์ชันนึงไปนั่นคือฟังก์ชัน <strong>fizzbuzz</strong> (เห็นคนชอบสอน TDD ด้วยตัวอย่างนี้สงสัยคงเป็นฟังก์ชันสำคัญแน่ๆ ทำไม lodash ไม่มีให้ใช้นะเลยต้องเพิ่มให้ซะหน่อย :P)</p>\n<blockquote>\n<p><strong>นิยาม:</strong>\n<strong>fizzbuzz</strong> เป็นฟังก์ชันที่รับ array ของตัวเลขเข้ามา และจะแปลงตัวเลขแต่ละตัวใน array ให้เป็น string คำว่า “fizz” ถ้าหาร 3 ลงตัว, เป็นคำว่า “buzz” ถ้าหาร 5 ลงตัว, และเป็นคำว่า “fizzbuzz” ถ้าหารทั้ง 3 และ 5 ลงตัว กรณีนอกเหนือจากนี้ได้เป็น string ของตัวเลขเดิม เช่น <code>_.fizzbuzz([1,2,3,4,5,6,15,20])</code> จะถูกแปลงเป็น <code>['1','2','fizz','4','fuzz','6','fizzbuzz','20']</code> เป็นต้น</p>\n</blockquote>\n<h2>ขั้นที่ 0 โครงสร้างโฟลเดอร์</h2>\n<p>หน้าตาโฟลเดอร์เราเดี๋ยวจะเป็นแบบนี้นะครับ</p>\n<pre><code class=\"language-javascript\">repo <span class=\"hljs-comment\">// โฟลเดอร์สำหรับเก็บโปรเจคต่างๆในเครื่องเรา</span>\n|-- lodash <span class=\"hljs-comment\">// เดี๋ยวเราจะ clone lodash มาไว้ตรงนี้</span>\n|-- <span class=\"hljs-keyword\">try</span>-fizzbuzz-lodash <span class=\"hljs-comment\">// โปรเจคของเราที่จะเรียกใช้ lodash</span>\n</code></pre>\n<h2>ขั้นที่ 1 สร้างโปรเจค</h2>\n<p>ในโฟลเดอร์ <code>try-fizzbuzz-lodash</code> ทำการ init repo ด้วยคำส่ัง\n<code>yarn init</code>\nหลังจากนั้นลองติดตั้ง lodash ปกติก่อนด้วยคำสั่ง\n<code>yarn add lodash</code>\nก็จะมีโฟลเดอร์ <code>try-fizzbuzz-lodash/node_modules/lodash</code> ขึ้นมาตามปกติให้เรียกใช้ได้</p>\n<p>แล้วเราก็ลองสร้างไฟล์ <code>index.js</code> ขึ้นมาใน <code>try-fizzbuzz-lodash</code> ดังนี้</p>\n<pre><code class=\"language-javascript\"><span class=\"hljs-keyword\">const</span> _ = <span class=\"hljs-built_in\">require</span>(<span class=\"hljs-string\">'lodash'</span>) <span class=\"hljs-comment\">// import lodash เข้ามา</span>\n<span class=\"hljs-keyword\">const</span> nums = _.range(<span class=\"hljs-number\">1</span>, <span class=\"hljs-number\">21</span>) <span class=\"hljs-comment\">// สร้าง array ของตัวเลขตั้งแต่ 1 ถึง 20</span>\n\n<span class=\"hljs-built_in\">console</span>.log(_.fizzbuzz(nums)) <span class=\"hljs-comment\">// แปลง array ให้เป็นค่า fizzbuzz</span>\n</code></pre>\n<p>หลังจากนั้น save ไฟล์แล้วรัน\n<code>node index.js</code></p>\n<p>ก็จะขึ้น error มาว่าตัว lodash ที่เราใช้ไม่มีฟังก์ชัน fizzbuzz</p>\n<pre><code class=\"language-bash\">console.log(_.fizzbuzz(nums))\n              ^\n\nTypeError: _.fizzbuzz is not a <span class=\"hljs-keyword\">function</span>\n    at Object.&lt;anonymous&gt; (/path/to/repo/try-fizzbuzz-lodash/index.js:4:15)\n</code></pre>\n<p>โอเคเป็นไปตามที่คิด เพราะตัวต้นฉบับไม่มีฟังก์ชัน fizzbuzz อยู่</p>\n<h2>ขั้นที่ 2 clone lodash</h2>\n<p>ในโฟลเดอร์  <code>repo</code> สั่ง</p>\n<pre><code class=\"language-bash\">git <span class=\"hljs-built_in\">clone</span> git@github.com:lodash/lodash.git\n</code></pre>\n<p>ก็จะได้โฟลเดอร์ lodash ต้นตำรับมา\n(จริงๆแนะนำว่าให้ fork ไปเป็นของตัวเองก่อน แล้วค่อย clone เผื่ออีกหน่อยอยากส่ง pull request กลับไปหาต้นน้ำ)</p>\n<p>เราจะทำการแก้ไข lodash ในโฟลเดอร์นี้กัน</p>\n<h2>ขั้นที่ 3 yarn link</h2>\n<p>เข้าไปในโฟลเดอร์ <code>repo/lodash</code> แล้วสั่งคำสั่ง</p>\n<pre><code class=\"language-bash\">yarn link\n</code></pre>\n<p>คำสั่งนี้จะทำการ register node module ในโฟลเดอร์ที่สั่งคำสั่งเข้ากับ global registry ของเครื่อง local ที่เราใช้งาน เพื่อให้ project อื่นของเราเรียกใช้งานได้</p>\n<h2>ขั้นที่ 4 yarn link package-name</h2>\n<p>กลับไปที่โฟลเดอร์ <code>repo/try-fizzbuzz-lodash</code> แล้วสั่งคำสั่ง</p>\n<pre><code class=\"language-bash\">yarn link lodash\n</code></pre>\n<p>คราวนี้เราจะทำการ link lodash ไปยังโฟลเดอร์ที่เรา register เอาไว้ในขั้นตอนก่อนหน้า</p>\n<p>ลองตรวจสอบด้วยคำสั่ง <code>ls -l node_modules</code> จะแสดงให้เห็นว่า lodash ของเรากลายเป็น symbolic link ไปที่ yarn global registry ในเครื่องเราแล้ว</p>\n<pre><code class=\"language-bash\">$ ls <span class=\"hljs-_\">-l</span> node_modules\ntotal 8\nlrwxr-xr-x  1 ping  staff  33 Dec 10 20:44 lodash -&gt; &lt;path&gt;/&lt;to&gt;/.yarn-cache/.link/lodash\n</code></pre>\n<h2>ขั้นที่ 5 แก้ไขโค้ด lodash</h2>\n<p>ที่เลือก lodash มาเพราะว่าการเพิ่มฟังก์ชันให้มันสามารถทำได้ง่ายมากเลย โดยแก้ในไฟล์ <code>repo/lodash/lodash.js</code></p>\n<p>ลองหาบรรทัดนี้</p>\n<pre><code class=\"language-javascript\">lodash.filter = filter;\nlodash.flatMap = flatMap;\n</code></pre>\n<p>แล้วแทรก <code>lodash.fizzbuzz = fizzbuzz;</code> เข้าไป (จริงๆไว้ที่ไหนก็ได้ แบบนี้แค่จะได้เรียงลำดับอักษรเฉยๆ)</p>\n<pre><code class=\"language-javascript\">lodash.filter = filter;\nlodash.fizzbuzz = fizzbuzz;\nlodash.flatMap = flatMap;\n</code></pre>\n<p>คือเป็นการเพิ่ม property ชื่อ fizzbuzz ให้กับ lodash โดยชี้ไปยังฟังก์ชันที่ชื่อ fizzbuzz\nดังนั้นก็ให้เราเพิ่มฟังก์ชัน fizzbuzz ก็ไปต่อจากฟังก์ชัน filter เลยล่ะกัน</p>\n<pre><code class=\"language-javascript\"><span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> <span class=\"hljs-title\">filter</span>(<span class=\"hljs-params\">collection, predicate</span>) </span>{...}\n\n<span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> <span class=\"hljs-title\">fizzbuzz</span>(<span class=\"hljs-params\">collection</span>) </span>{\n  <span class=\"hljs-keyword\">return</span> collection.map(<span class=\"hljs-function\"><span class=\"hljs-params\">num</span> =&gt;</span>\n    (num % <span class=\"hljs-number\">3</span> === <span class=\"hljs-number\">0</span> ? <span class=\"hljs-string\">'fizz'</span> : <span class=\"hljs-string\">''</span>) + (num % <span class=\"hljs-number\">5</span> === <span class=\"hljs-number\">0</span> ? <span class=\"hljs-string\">'buzz'</span> : <span class=\"hljs-string\">''</span>)\n    || <span class=\"hljs-built_in\">String</span>(num)\n  )\n}\n</code></pre>\n<p>โค้ดเขียนแบบสนุกๆโดยใช้ trick ของ javascript เล็กน้อย เอาเป็นว่าฟังก์ชันนี้ก็เช็ค fizzbuzz แบบปกตินั่นแหล่ะแล้ว return ค่าเป็น array ที่ element แต่ละตัวถูกแปลงค่าแล้ว (ไม่ขอลงรายละเอียดเพราะไม่ใช่ประเด็นของบทความนี้)</p>\n<p>เมื่อเสร็จแล้วก็ลองไปที่โฟลเดอร์ <code>try-fizzbuzz-lodash</code> แล้วสั่งรัน <code>node index.js</code> อีกรอบนึงดูก็จะเห็นว่าสามารถเรียกใช้ lodash ที่อยู่ในเครื่อง local เราได้แล้ว!!!</p>\n<pre><code class=\"language-bash\">$ node index.js\n[ <span class=\"hljs-string\">'1'</span>,\n  <span class=\"hljs-string\">'2'</span>,\n  <span class=\"hljs-string\">'fizz'</span>,\n  <span class=\"hljs-string\">'4'</span>,\n  <span class=\"hljs-string\">'buzz'</span>,\n  <span class=\"hljs-string\">'fizz'</span>,\n  <span class=\"hljs-string\">'7'</span>,\n  <span class=\"hljs-string\">'8'</span>,\n  <span class=\"hljs-string\">'fizz'</span>,\n  <span class=\"hljs-string\">'buzz'</span>,\n  <span class=\"hljs-string\">'11'</span>,\n  <span class=\"hljs-string\">'fizz'</span>,\n  <span class=\"hljs-string\">'13'</span>,\n  <span class=\"hljs-string\">'14'</span>,\n  <span class=\"hljs-string\">'fizzbuzz'</span>,\n  <span class=\"hljs-string\">'16'</span>,\n  <span class=\"hljs-string\">'17'</span>,\n  <span class=\"hljs-string\">'fizz'</span>,\n  <span class=\"hljs-string\">'19'</span>,\n  <span class=\"hljs-string\">'buzz'</span> ]\n</code></pre>\n<h2>ขั้นที่ 6 เปลี่ยนกลับไปใช้ lodash บน npm</h2>\n<p>สมมติว่าเราอยากกลับเปลี่ยนกลับไปใช้ lodash ดั้งเดิมบน npm เราสามารถยกเลิกการ link lodash บนเครื่อง local ได้โดยให้อยู่ที่โฟลเดอร์ <code>try-fizzbuzz-lodash</code> แล้วสั่ง</p>\n<pre><code class=\"language-bash\">yarn unlink lodash\n</code></pre>\n<p>แล้ว lodash ใน node_modules ของเราก็จะถูกลบไป แล้วให้ติดตั้ง lodash ตัวปกติด้วยคำสั่ง <code>yarn add lodash</code> ตามเดิม</p>\n<p>ถ้าเกิดเราอยากลบ lodash ออกจาก local registry อย่างถาวรไม่ให้ใคร link ถึงได้อีกให้ไปที่โฟลเดอร์ <code>repo/lodash</code> ที่เรา clone มาแล้วสั่งคำสั่ง</p>\n<pre><code class=\"language-bash\">yarn unlink\n</code></pre>\n<p>เป็นอันเสร็จพิธี</p>\n<h2>สรุป</h2>\n<p>คำสั่งที่ใช้ในการ link local package มีดังนี้ครับ</p>\n<ul>\n<li><code>yarn link</code> ใน local package ที่เราจะใช้เพื่อ register กับ local registry</li>\n<li><code>yarn link &lt;package-name&gt;</code> ในโปรเจคที่เราจะเรียกใช้ package ที่เรา link ไว้</li>\n<li><code>yarn unlink &lt;package-name&gt;</code> เพื่อยกเลิกการใช้ตัวที่เรา link ไว้</li>\n<li><code>yarn unlink</code> ใน local package เพื่อ unregister ออกจาก local registry</li>\n</ul>\n<p>ขอให้สนุกกับการเขียนโปรแกรมครับ</p>\n<p>Happy Hacking</p>\n<p>ป.ล. ทิ้งท้ายสำหรับคนที่รอเรียนคอร์ส React กับทางทีม CodeSheep เดือนธันวานี้งานเข้าหนักหน่วง เลยคิดว่าจะเปิดสอนอีกทีเดือนมกราคมนะครับ ใครสนใจติดตามประกาศได้ที่เพจ <a href=\"https://www.facebook.com/codesheep/\">CodeSheep</a> นะครับ</p>\n"}

