	module.exports = {"layout":"post","title":"แกะโค้ด react-router v4 - ตอนที่ 1 Object.create(null)","summary":"วันนี้มาลองเขียนบทความให้สมชื่อ CodeSheep หรือแกะโค้ดกันนะครับ เป็นแนวบทความที่อยากลองนำโค้ดที่อยู่ในโปรเจค Open Source ดังๆ มาแกะดูว่าเค้าใช้เทคนิกอะไรในการเขียนกันบ้าง (อ่านต่อ)","tags":["react-router","javascript","prototype"],"img":"https://raw.githubusercontent.com/LarngearTech/codesheep.io/master/pages/2017-01-01-react-router-object-create-null/react_router_v4.png","author":"Supasate Choochaisri","body":"<p>วันนี้มาลองเขียนบทความให้สมชื่อ CodeSheep หรือแกะโค้ดกันนะครับ เป็นแนวบทความที่อยากลองนำโค้ดที่อยู่ในโปรเจค Open Source ดังๆ มาแกะดูว่าเค้าใช้เทคนิกอะไรในการเขียนกันบ้าง โดยจะพยายามแยกแต่ละเรื่องในโค้ดเป็น 1 ตอนถ้าสามารถแยกได้นะครับ</p>\n<h2>Project: React Router v4 alt branch</h2>\n<p>โปรเจคที่เรามาแกะกันคือ React Router ถ้าใครใช้ React คงจะรู้จักกันดี แต่ถ้าใครไม่รู้จักก็ไม่เป็นไรครับ เพราะโค้ดส่วนที่จะแกะเป็นความรู้ทั่วไปใช้กับโปรเจคไหนก็ได้ ณ วันที่เขียนอยู่นี้รุ่น stable ยังเป็น v3 ส่วน v4 ยังเป็น alpha อยู่ นอกจากนั้น v4 ยังมี branch คู่ขนานชื่อ <code>v4-alt</code> ซึ่งเป็นการ rewrite v4 ขึ้นมาอีกรอบโดยยังไม่สรุปว่าสุดท้ายแล้วจะใช้ branch ไหน แต่ในโค้ดตัวอย่างที่เราจะแกะวันนี้จะใช้ branch <code>v4-alt</code> กันครับ</p>\n<img src='./v4-alt.png' width=\"300px\"/>\n<p>โดยโค้ดเราจะอ้างอิงจาก <a href=\"https://github.com/ReactTraining/react-router/tree/f828a0c21f82309bebc55bdd438faaae078cbaf9\">commit นี้</a> ซึ่งเป็น commit ล่าสุด ณ วันที่เขียนบทความนี้</p>\n<h2>matchPattern.js</h2>\n<p>ไฟล์ที่เราจะมาแกะกันคือไฟล์ <code>modules/matchPattern.js</code> ซึ่งมีหน้าที่ในการทดสอบว่า URL ที่ router ได้รับนั้นตรงกับ pattern ที่ระบุไว้หรือไม่</p>\n<p>pattern หน้าตาเป็นแนวแบบนี้ครับ</p>\n<ul>\n<li><code>/</code> จะ match กับ root domain</li>\n<li><code>/users</code> จะ match <code>/users</code></li>\n<li><code>/users/:userId</code> จะ match URL param เช่น <code>/users/1</code>, <code>/users/4</code> เป็นต้น</li>\n</ul>\n<p>โดยเราสามารถระบุค่า exact ว่าต้อง match แบบเป๊ะๆหรือไม่นะครับ เช่น pattern <code>/</code> ถ้าเราระบุ exact นั้นตัว URL จะต้องเป็น <code>/</code> เท่านั้น</p>\n<p>แต่ถ้าเราไม่ต้องการแบบ exact แล้วถ้า URL เป็น <code>/users</code> ก็จะถือว่า match กับ pattern ‘/’ เหมือนกันครับ เพราะมี <code>/</code> อยู่ใน URL</p>\n<p>ขอพูดคร่าวๆแค่นี้ ไม่งั้นเดี๋ยวจะกลายเป็นบทความสอนใช้ React Router v4 ไป เอาเป็นว่าลองมาดูโค้ดเต็มๆของไฟล์นี้กันเลยดีกว่าครับ ไฟล์เล็กๆน่ารัก 41 บรรทัด แต่มีจุดที่น่าสนใจหลายที่ (ยังไม่ต้องสนใจโค้ดก็ได้ครับ แค่ยกมาให้ดูหน้าตาคร่าวๆของมันเฉยๆ)</p>\n<p><a href=\"https://github.com/ReactTraining/react-router/blob/f828a0c21f82309bebc55bdd438faaae078cbaf9/modules/matchPattern.js\"><code>/modules/matchPattern.js</code></a></p>\n<pre><code class=\"language-js\"><span class=\"hljs-keyword\">import</span> pathToRegexp <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'path-to-regexp'</span>\n\n<span class=\"hljs-keyword\">const</span> patternCache = <span class=\"hljs-built_in\">Object</span>.create(<span class=\"hljs-literal\">null</span>)\n\n<span class=\"hljs-keyword\">const</span> compilePattern = <span class=\"hljs-function\">(<span class=\"hljs-params\">pattern</span>) =&gt;</span> {\n  <span class=\"hljs-keyword\">if</span> (!patternCache[pattern]) {\n    <span class=\"hljs-keyword\">const</span> keys = []\n    <span class=\"hljs-keyword\">const</span> re = pathToRegexp(pattern, keys)\n    patternCache[pattern] = { re, keys }\n  }\n\n  <span class=\"hljs-keyword\">return</span> patternCache[pattern]\n}\n\n<span class=\"hljs-keyword\">const</span> matchPattern = <span class=\"hljs-function\">(<span class=\"hljs-params\">pattern, exact, pathname</span>) =&gt;</span> {\n  <span class=\"hljs-keyword\">if</span> (!pattern)\n    <span class=\"hljs-keyword\">return</span> { pathname, <span class=\"hljs-attr\">isExact</span>: <span class=\"hljs-literal\">true</span>, <span class=\"hljs-attr\">params</span>: {} }\n\n  <span class=\"hljs-keyword\">const</span> { re, keys } = compilePattern(pattern)\n  <span class=\"hljs-keyword\">const</span> match = re.exec(pathname)\n\n  <span class=\"hljs-keyword\">if</span> (!match)\n    <span class=\"hljs-keyword\">return</span> <span class=\"hljs-literal\">null</span>\n\n  <span class=\"hljs-keyword\">const</span> [ path, ...values ] = match\n  <span class=\"hljs-keyword\">const</span> isExact = pathname === path\n\n  <span class=\"hljs-keyword\">if</span> (exact &amp;&amp; !isExact)\n    <span class=\"hljs-keyword\">return</span> <span class=\"hljs-literal\">null</span>\n\n  <span class=\"hljs-keyword\">return</span> {\n    isExact,\n    <span class=\"hljs-attr\">pathname</span>: path,\n    <span class=\"hljs-attr\">params</span>: keys.reduce(<span class=\"hljs-function\">(<span class=\"hljs-params\">memo, key, index</span>) =&gt;</span> {\n      memo[key.name] = values[index]\n      <span class=\"hljs-keyword\">return</span> memo\n    }, {})\n  }\n}\n\n<span class=\"hljs-keyword\">export</span> <span class=\"hljs-keyword\">default</span> matchPattern\n</code></pre>\n<p>ในตอนนี้เราจะมาดูที่บรรทัดที่ 3 กันครับ</p>\n<pre><code class=\"language-js\"><span class=\"hljs-keyword\">const</span> patternCache = <span class=\"hljs-built_in\">Object</span>.create(<span class=\"hljs-literal\">null</span>)\n</code></pre>\n<p>ซึ่งตามชื่อเลยคือเป็น cache ของ pattern ถ้ามี pattern นี้อยู่ใน cache แล้วก็ไม่ต้อง process ซ้ำ ตอบกลับจากค่าใน cache ได้ทันที ลองดูตัวอย่างที่เค้าใช้ในฟังก์ชัน <code>compilePattern</code> ก็ได้ครับ</p>\n<p>คำถามคือ ทำไมต้องใช้ <code>const patternCache = Object.create(null)</code> ทำไมไม่ใช้ <code>const patternCache = {}</code> ซึ่งเป็น JavaScript Object ที่ไว้เก็บ key, value ได้เหมือนกัน? ซึ่งเป็นเนื้อหาหลักของ blog ตอนนี้ครับ</p>\n<h2>const patternCache = Object.create(null)</h2>\n<p>ฟังก์ชัน <code>Object.create</code> ใช้ในการสร้าง JavaScript object ใหม่ขึ้น โดยสามารถระบุได้ว่าจะให้ inherit property มาจาก prototype ตัวไหน (prototype คล้ายๆกับ instance ใน OOP)</p>\n<p>ลองดูตัวอย่างนี้</p>\n<pre><code class=\"language-js\"><span class=\"hljs-keyword\">const</span> a = { <span class=\"hljs-attr\">val</span>: <span class=\"hljs-number\">10</span>, <span class=\"hljs-attr\">chrArr</span>: [<span class=\"hljs-string\">'x'</span>, <span class=\"hljs-string\">'y'</span>] }\n<span class=\"hljs-keyword\">const</span> b = <span class=\"hljs-built_in\">Object</span>.create(a)\n<span class=\"hljs-built_in\">console</span>.log(b.val) <span class=\"hljs-comment\">// 10</span>\n<span class=\"hljs-built_in\">console</span>.log(b.chrArr) <span class=\"hljs-comment\">// ['x', 'y']</span>\n</code></pre>\n<p>จะเห็นว่า <code>b</code> ได้ property ทั้งหมดของ <code>a</code> มาด้วย โดย property ไหนเป็น reference เราก็จะได้ reference ที่ชี้ไปที่เดียวกัน เช่น <code>charArr</code> เป็น reference ก็จะชี้ไปที่ <code>charArr</code> ที่ <code>a</code> ได้สร้างเอาไว้</p>\n<p>ลองทดสอบดูนะจะได้ตามตัวอย่างนี้</p>\n<pre><code class=\"language-js\"><span class=\"hljs-built_in\">console</span>.log(b.chrArr === a.chrArr) <span class=\"hljs-comment\">// true</span>\nb.chrArr[<span class=\"hljs-number\">0</span>] = <span class=\"hljs-string\">'z'</span> <span class=\"hljs-comment\">// เปลี่ยนค่าใน array ที่เป็น reference</span>\n<span class=\"hljs-built_in\">console</span>.log(b.chrArr) <span class=\"hljs-comment\">// ['z', 'y']</span>\n<span class=\"hljs-built_in\">console</span>.log(a.chrArr) <span class=\"hljs-comment\">// ['z', 'y'] ค่าที่ a เปลี่ยนด้วยเพราะชี้ไปที่เดียวกัน</span>\n</code></pre>\n<p>ดังนั้นการที่เราสั่ง <code>Object.create(null)</code> ขึ้นมา โดย inherit property จาก null ซึ่งก็คือไม่มี property ใดๆ ทำให้ได้ JavaScript Object เปล่าๆขึ้นมานั่นเอง</p>\n<pre><code class=\"language-js\"><span class=\"hljs-keyword\">const</span> a = <span class=\"hljs-built_in\">Object</span>.create(<span class=\"hljs-literal\">null</span>)\n<span class=\"hljs-built_in\">console</span>.log(a) <span class=\"hljs-comment\">// ได้ Object {} และไม่มี property ใดๆ</span>\n</code></pre>\n<p>ซึ่งก็เลยนำมาใช้เป็น cache แบบ key-value ที่ไม่มี key ใดๆเป็นค่าเริ่มต้น</p>\n<h2>const patternCache = {}</h2>\n<p>คราวนี้มาดูกันว่า <code>Object.create(null)</code> มันต่างกับ <code>{}</code> ยังไง ลองดูจากผลลัพธ์ของโค้ดนี้กันนะครับ</p>\n<p><img src=\"./empty_object.png\" alt=\"Empty Object\"></p>\n<p>จะเห็นว่าการสร้าง <code>const a = {}</code> จะทำให้ <code>a</code> ได้ inherit property ของ <code>Object.prototype</code> มาด้วย (ลองทดสอบกันเองด้วย <code>console.log(Object.prototype)</code> ดูนะครับ)</p>\n<p>ดังนั้นจริงๆ แล้ว <code>const a = {}</code> นั้นมีค่าเท่ากับ <code>const a = Object.create(Object.prototype)</code> นั่นเอง</p>\n<h2>สรุป</h2>\n<p>ดังนั้นเหตุผลที่เค้าไม่ใช้ <code>const patternCache = {}</code> แต่เลือกใช้ <code>const patternCache = Object.create(null)</code> ก็เพราะว่าเค้าต้องการ cache ที่ไม่มี key ใดๆติดมานั่นเอง (ถ้าสร้างด้วย <code>const patternCache = {}</code> แล้วสั่ง <code>if (patternCache['toString'])</code> จะได้ค่าเป็น <code>true</code> แม้ว่าจะไม่เคย cache ค่านี้มาก่อน)</p>\n<p>เดี๋ยวในตอนต่อไปจะมาแกะโค้ดไฟล์นี้กันต่อนะครับ</p>\n<p>และสุดท้ายนี้ก็ขอสวัสดีปีใหม่ชาวโปรแกรมเมอร์ไทยทุกคนนะครับ</p>\n<p>Happy New Year 2017 :)</p>\n"}

