	module.exports = {"layout":"post","title":"แผนผัง Redux Architecture","summary":"คิดว่าหลายๆคนที่เคยเขียน Redux กันมาบ้าง คงจะคุ้นเคยกับคำว่า Uni-Directional Data Flow ซึ่งได้อิทธิพลมาจาก Flux Architecture แต่ว่าพอไปเริ่มเขียนกันจริงแล้ว ช่วงแรกๆจะเห็นภาพไม่ค่อยชัดว่ามันสัมพันธ์กับแต่ละ component ที่เราเขียนยังไง ในบทความนี้ผมเลยนำแผนผัง Redux Architecture ที่ทำขึ้นมาเพื่อใช้สอนใน Bootcamp ของ CodeSheep มาให้ดูกันนะครับ (อ่านต่อ)","tags":["redux","react","architecture"],"img":"https://raw.githubusercontent.com/LarngearTech/codesheep.io/master/pages/2017-01-06-redux-architecture/redux_architecture.png","author":"Supasate Choochaisri","body":"<p>คิดว่าหลายๆคนที่เคยเขียน Redux กันมาบ้าง คงจะคุ้นเคยกับคำว่า Uni-Directional Data Flow ซึ่งได้อิทธิพลมาจาก Flux Architecture แต่ว่าพอไปเริ่มเขียนกันจริงแล้ว ช่วงแรกๆจะเห็นภาพไม่ค่อยชัดว่ามันสัมพันธ์กับแต่ละ component ที่เราเขียนยังไง ในบทความนี้ผมเลยนำแผนผัง Redux Architecture ที่ทำขึ้นมาเพื่อใช้สอนใน Bootcamp ของ CodeSheep มาให้ดูกันนะครับ</p>\n<p>หมายเหตุ บทความนี้เหมาะสำหรับคนที่เคยเขียน redux มาก่อนแล้ว แต่ถ้าไม่เคยเขียนมาก่อนก็ไม่ต้องสนใจส่วนที่เป็นโค้ดก็ได้ครับ</p>\n<h1>Simplified Redux Architecture</h1>\n<p><img src=\"./simplified_redux_architecture.png\" alt=\"Simplified Redux Architecture\"></p>\n<p>เริ่มจากอันแรกเป็นแผนผังให้เห็นภาพรวมแบบง่ายๆ ที่หลายๆที่ก็ทำออกมาคล้ายๆแบบนี้กัน มีจุดที่น่าสนใจดังนี้ครับ</p>\n<h2>Store</h2>\n<ol>\n<li>state และ reducer อยู่ใน store ซึ่งช่วยให้เราจำได้ว่าตอนเราสร้าง store ด้วยคำสั่ง <code>createStore</code> เราเลยต้องส่ง <code>initialState</code> กับ <code>rootReducer</code> เป็น parameters เพื่อสร้างก้อน store นี้ขึ้นมานั่นเอง</li>\n<li>store จะมีคำสั่ง <code>dispatch</code> เป็น API ให้เรียกใช้ได้ ถ้าจำกันได้ตอนเราทำ <code>mapDispatchToProps</code> เราจะเรียกคำสั่ง <code>dispatch</code> ได้ ซึ่งคำสั่งนี้ก็คือตัวเดียวกับ <code>store.dispatch</code> นั่นเอง</li>\n</ol>\n<h2>Simplified Data Flow</h2>\n<p>การวิ่งของข้อมูลที่เป็นทิศทางเดียว (Uni-directional Data Flow) จะแทนด้วยลูกศรสีเหลืองกรอบม่วง โดยภาพรวมแบบง่ายๆ เป็นดังนี้ครับ</p>\n<ol>\n<li>เริ่มต้นจากกล่อง state วิ่งลงมายัง View หรือ Component ของเรา (เข้ามาเป็น props)</li>\n<li>เมื่อ View ต้องการเปลี่ยนค่าใน store จะเรียก <code>dispatch</code> เพื่อส่ง action ไปยัง reducer</li>\n<li>Reducer เป็นฟังก์ชันที่รับค่า state ปัจจุบัน และ action เข้ามา แล้วคืนค่าออกมาเป็น state ถัดไป</li>\n<li>เมื่อ state เกิดการเปลี่ยนแปลงก็จะส่งค่ากลับลงมาเพื่ออัพเดท View ผ่านทาง props</li>\n</ol>\n<p>สังเกตว่าในภาพ state เป็นกล่องเดียว แต่จริงๆทุกครั้งที่ state เปลี่ยนจะเกิดกล่องใหม่ของส่วนที่เปลี่ยนแปลงขึ้นมา โดยเราจะไม่ไปแก้ไขค่าในกล่อง state เดิม (ตามหลักการของ pure function ที่จะไม่ก่อให้เกิด side effect)</p>\n<p>พอเห็นภาพรวมง่ายๆแบบนี้แล้ว เดี๋ยวเราลองไปดูแบบเต็มๆกันครับ</p>\n<h1>(Almost Complete) Redux Architecture</h1>\n<p><img src=\"./redux_architecture.png\" alt=\"Redux Architecture\"></p>\n<p>จะเห็นว่าพอเรามาเขียนโค้ดจริงๆมันมีหลายละเอียดปลีกย่อยเพิ่มมามาก ที่ใส่คำว่า <strong>almost complete</strong> คือจริงๆมันยังมีรายละเอียดมากกว่านี้ที่เรามองไม่เห็นและไม่ได้ใช้โดยตรง แผนภาพนี้เลยเน้นเฉพาะที่เกี่ยวข้องตอนเราใช้กันจริงๆครับ</p>\n<h2>Store</h2>\n<ol>\n<li>State object ของเรามีลักษณะเป็น key-value ทั่วไป แต่ละ key (เช่น auth, todos ในภาพ) ก็จะมี reducer ที่รับผิดชอบอยู่</li>\n<li>Root Reducer คือที่เราสร้างจากฟังก์ชัน <code>combineReducers</code> นั่นเอง ก็จะประกอบด้วย reducer แต่ละตัวที่รับผิดชอบแต่ละ sub-state ตาม key ของ state object</li>\n</ol>\n<pre><code class=\"language-js\"><span class=\"hljs-keyword\">const</span> rootReducer = combineReducers({\n  <span class=\"hljs-attr\">auth</span>: authReducer,\n  <span class=\"hljs-attr\">todos</span>: todoReducer,\n})\n</code></pre>\n<ol start=\"3\">\n<li>Middlewares ก็จะมีหลายตัว โดยภาพในตัวอย่างเราอาจมี middleware ตัวอื่นๆ ก่อนที่จะไปถึง redux-thunk middleware เป็นต้น</li>\n<li>จากข้อ 1 - 3 จะเห็นภาพชัดขึ้นว่าฟังก์ชัน <code>createStore</code> ที่ได้พูดไปแล้วมันต้องประกอบด้วยอะไรบ้างถึงได้เป็น store ขึ้นมา ก็คือ <code>initialState</code>, <code>rootReducer</code>, และ <code>applyMiddleware</code> ที่เราใช้กันนั่นเอง</li>\n</ol>\n<pre><code class=\"language-js\"><span class=\"hljs-keyword\">const</span> store = createStore(\n  initialState,\n  rootReducer,\n  applyMiddleware(\n    thunk,\n    otherMiddleware,\n  )\n)\n</code></pre>\n<ol start=\"5\">\n<li>นอกจากฟังก์ชัน <code>dispatch</code> แล้ว store ยังมี API อื่นๆอีก ตัวที่ใช้กันโดยไม่รู้ตัวก็คือ <code>store.subscribe</code> ที่จะคอยเรียก callback ให้ถ้า state มีการเปลี่ยนแปลง ซึ่งตัวที่แอบทำการ subscribe ให้เราก็คือฟังก์ชัน <code>connect</code> นั่นเอง โดยมันจะไป subscribe store ที่อยู่ใน context (อันนี้ขอไม่ลงรายละเอียดเรื่อง context ให้นึกว่ามันคล้ายๆ global object ที่ทำให้ส่งค่าต่างๆไปยังทุก component ได้โดยไม่ต้องส่งผ่าน props) ที่ component ชื่อ <code>Provider</code> เป็นคนจัดเตรียมไว้ให้ นั่นเป็นเหตุผลว่าทำไมเราต้องมี <code>Provider</code> ครอบโค้ดของเราและรับ store เป็น props เข้าไป</li>\n</ol>\n<pre><code class=\"language-js\">&lt;Provider store={store}&gt;\n  ...\n&lt;/Provider&gt;\n</code></pre>\n<h2>Data Flow</h2>\n<p>ลองมาดูการวิ่งของข้อมูลแบบเต็มๆ กัน ขอแปะภาพเดิมซ้ำอีกที่จะได้ไม่ต้องเลื่อนขึ้นไปดูนะครับ</p>\n<p><img src=\"./redux_architecture.png\" alt=\"Redux Architecture\"></p>\n<ol>\n<li>เริ่มต้นจากกล่อง state โดยตอนเริ่มต้น container component ที่ครอบ presentational component ของเรา (ที่สร้างจากฟังก์ชัน <code>connect</code>) จะดึงค่า state และคำสั่ง dispatch จาก store มาให้ แล้วโยนต่อไปให้ <code>mapStateToProps</code> และ <code>mapDispatchToProps</code> เพื่อส่งเข้าไปเป็น props ให้เรา</li>\n</ol>\n<pre><code class=\"language-js\"><span class=\"hljs-keyword\">const</span> mapStateToProps = <span class=\"hljs-function\"><span class=\"hljs-params\">state</span> =&gt;</span> ({\n  <span class=\"hljs-attr\">auth</span>: state.auth,\n  <span class=\"hljs-attr\">todos</span>: state.todos,\n})\n<span class=\"hljs-keyword\">const</span> mapDispatchToProps = <span class=\"hljs-function\"><span class=\"hljs-params\">dispatch</span> =&gt;</span> ({\n  <span class=\"hljs-attr\">fetchTodos</span>: <span class=\"hljs-function\"><span class=\"hljs-params\">()</span> =&gt;</span> dispatch(fetchTodos())\n})\n<span class=\"hljs-keyword\">export</span> <span class=\"hljs-keyword\">default</span> connect(mapStateToProps, mapDispatchToProps)(TodoList)\n</code></pre>\n<ol start=\"2\">\n<li>Presentational Component ทำการเรียกฟังก์ชันที่จะทำการ dispatch โดยอาจจะ dispatch เป็น action object หรือเป็น action function (ถ้ามีการใช้ redux-thunk middleware)</li>\n<li>ระหว่างการ dispatch กับ reducer จะมี middleware ทั้งหลายคั่นอยู่ โดยถ้ามีการใช้ redux-thunk ก็จะมีการตรวจสอบว่า action ที่รับเข้ามานั้นเป็น object หรือเป็น function</li>\n</ol>\n<ul>\n<li>ถ้าเป็น function จะทำการประมวลผล function นั้นก่อน ซึ่งในฟังก์ชันนั้นอาจจะมีการ dispatch อย่างอื่นออกมาอีกก็ได้</li>\n</ul>\n<pre><code class=\"language-js\"><span class=\"hljs-keyword\">const</span> fetchTodo = <span class=\"hljs-function\"><span class=\"hljs-params\">()</span> =&gt;</span> dispatch =&gt; {\n  fetch(...)\n    .then(<span class=\"hljs-function\"><span class=\"hljs-params\">todos</span> =&gt;</span> dispatch(fetchTodosSuccess(todos)))\n}\n</code></pre>\n<ul>\n<li>ถ้าเป็น object ก็จะส่งไปเข้า root reducer ทันที</li>\n</ul>\n<pre><code class=\"language-js\"><span class=\"hljs-keyword\">const</span> fetchTodosSuccess = <span class=\"hljs-function\">(<span class=\"hljs-params\">todos</span>) =&gt;</span> ({\n  <span class=\"hljs-attr\">type</span>: FETCH_TODOS_SUCCESS,\n  <span class=\"hljs-attr\">payload</span>: todos,\n})\n</code></pre>\n<ol start=\"4\">\n<li>ที่ rootReducer เป็นจุดที่คนเข้าใจผิดกันมากว่า action แต่ละประเภทจะไปเข้าแต่ละ reducer ที่เกี่ยวข้อง แต่จริงๆแล้ว <strong>ทุก</strong> action จะไปเข้า <strong>ทุก</strong> reducer ทั้งหมด แล้วแต่ละ reducer ถึงไปเลือกเองว่าถ้าไม่ต้องการก็ return state เดิมที่เป็นอยู่ไม่ต้องทำอะไร (ที่เรามักจะปล่อยผ่านด้วย <code>default</code> ใน <code>switch case</code> นั่นเอง)</li>\n<li>แต่ละ reducer ก็จะได้รับ parameter เป็น action object และ sub-state เฉพาะ key ที่มันรับผิดชอบมาเท่านั้น</li>\n</ol>\n<pre><code class=\"language-js\"><span class=\"hljs-keyword\">const</span> todoReducer = <span class=\"hljs-function\">(<span class=\"hljs-params\">state, action</span>) =&gt;</span> { <span class=\"hljs-comment\">// state ตรงนี้เป็นเพียง state.todos ของ root state tree</span>\n  ...\n}\n</code></pre>\n<p>และก็จะ return ค่าเป็น state ถัดไปออกมา</p>\n<ol start=\"6\">\n<li>เมื่อ state เปลี่ยน ก็จะ trigger container component ที่ subscribe store อยู่เพื่อไปอัพเดท props ของ presentation component นั่นเอง</li>\n</ol>\n<p>ก็จะจบรอบการทำงาน 1 รอบของ ​Redux</p>\n<h2>ทิ้งท้าย</h2>\n<p>ก็หวังว่าแผนภาพ Redux Architecture นี้จะช่วยให้เห็นภาพรวมการเชื่อมโยงของแต่ละส่วนใน Redux มากขึ้นนะครับ ช่วงแรกๆ ที่ฝึก Redux จะรู้สึกว่ายาก เพราะว่า learning curve ค่อนข้างสูง แต่พอเข้าใจแล้วที่เหลือก็จะเป็น pattern เดิมๆ ซ้ำไปมาตลอดครับ อารมณ์เดียวกับ React เลย แนะนำว่าให้ลองเขียนแอพ TodoMVC ขึ้นมาเล่นโดยไม่ดูเฉลยซัก 3 รอบก็จะทำให้เข้าใจการทำงานของ redux แน่นขึ้นมากล่ะครับ</p>\n"}

